export default {
	data() {
		return {
			
		}
	},
	methods: {
		handleGetUserInfo(res){
			if(res.detail.userInfo){ // 用户授权
				wx.setStorage({
					key: 'userInfo',
					data: JSON.stringify(res.detail.userInfo)
				})
				wx.reLaunch({
					url: '/pages/personal/personal'
				})
			}
		}
	}
}
